import { Component, OnInit } from '@angular/core';
import { AuthServiceAPI } from '../shared/auth.service';
import { Router } from '@angular/router';

@Component({
    selector: 'app-profile',
    templateUrl: './profile.component.html',
    styleUrls: ['./profile.component.scss']
})

export class ProfileComponent implements OnInit {

    user = {userName: '', firstName: '', lastName: '', country: null, phone: null, profileImg: {}}
    imgURL : any;
    selectedFile;

    constructor(private authApi: AuthServiceAPI,
        private router: Router) { }

    ngOnInit() {
        if(!this.authApi.isLoggedIn()){
            this.router.navigateByUrl('/');
        } else {
            this.user.userName = this.authApi.getUser();
            this.authApi.getUserDetails(this.user.userName).subscribe(data=>{
                console.log(data);
                this.user.firstName = data.firstName;
                this.user.lastName = data.lastName;
                this.user.country = data.country!=null ? data.country:null;
                this.user.phone = data.phone!=null ? data.phone:null;
                if(data.profileImg!=null)
                    this.imgURL = 'data:image/png;base64,'+ data.profileImg.pic;
                else if(data.socialImage!=null)
                    this.imgURL = data.socialImage;
            })
        }
    }

    onFileChange(event){
        console.log(event);
        this.selectedFile = event.target.files[0];
        console.log(this.selectedFile);
        let reader = new FileReader();
        reader.readAsDataURL(this.selectedFile);
        reader.onload = (event1)=>{
            this.imgURL = reader.result;
        }
    }

    updateProfile(){
        
            const uploadData = new FormData();
            if(this.selectedFile!=null){
                uploadData.append("profileImg", this.selectedFile, this.selectedFile.name);
            } else {
                uploadData.append("profileImg", null);
            }
            uploadData.append("userName", this.user.userName);
            uploadData.append("firstName", this.user.firstName);
            uploadData.append("lastName", this.user.lastName);
            uploadData.append("country", this.user.country);
            uploadData.append("phone", this.user.phone);
            console.log(uploadData.get("profileImg"))
            this.authApi.updateUser(uploadData).subscribe(data=>{
                console.log(data);
                alert("Profile Updated");
                window.location.reload();
            });
    }

}
