import { Component, OnInit, Input } from '@angular/core';
import { EncryptService } from '../shared/encode.service';
import { AuthServiceAPI } from '../shared/auth.service';
import { Questions } from '../shared/Questions';
import { ConstantsService } from '../shared/constants.service';
import { AuthService, GoogleLoginProvider} from 'angularx-social-login';
import { Router } from '@angular/router';

@Component({
    selector: 'app-signup',
    templateUrl: './signup.component.html',
    styleUrls: ['./signup.component.scss']
})
export class SignupComponent implements OnInit {
    focus;
    focus1;
    focus2;
    focus3;
    focus4;
    focus5;
    focus6;
    passwordStregth;
    passwordError;
    allQtns: Questions[];
    default: Questions;
    placeholder=" Select security question";
    created = false;

    @Input()
    user = {userName: '', password: '', firstName:'', lastName: '', securityQuestion: new Questions, answer: '', social:false, socialImage:null}

    @Input()
    Re_password;

    @Input()
    password;

    constructor(public authApi: AuthServiceAPI,
                public encryption: EncryptService,
              private constant: ConstantsService,
              private authService: AuthService,
              private router: Router) { }

    ngOnInit() {
        this.allQtns = this.constant.questions;
    }

    onChange(){
        this.placeholder = "";
    }

    signup() {
        if(this.password === this.Re_password) {
            this.created = true;
            this.user.password = this.encryption.encrypt(this.password);
            console.log(this.user);
            console.log(JSON.stringify(this.user));
            console.log(this.constant.baseApiUrl);
            this.authApi.createUser(this.user).subscribe((data) => {
                console.log(data);
                alert("Please verify the email to complete registration.");
                window.location.reload();
            })
        }
        else {
            this.passwordError = "Password do not match";
        }
    }

    signupGoogle(){
        this.authService.signIn(GoogleLoginProvider.PROVIDER_ID).then(userData => {
            console.log(userData);
            this.user.firstName = userData.firstName;
            this.user.lastName = userData.lastName;
            this.user.userName = userData.email;
            this.user.social = true;
            this.user.socialImage = userData.photoUrl;
            this.user.securityQuestion.id = "c5d26eca-9c2c-46c0-ca73-50f615cce67c";
            this.user.securityQuestion.question = "Google Account";
            this.user.securityQuestion.shortName = "google";
            this.authApi.createUser(this.user).subscribe((data) => {
                console.log(data);
                sessionStorage.setItem("currentUser", JSON.stringify(data.userName));
                this.authApi.ISSOCIAL = true;
                if(data.country != null && data.phone != null){
                    this.authApi.setUpdated(true);
                }
                this.router.navigateByUrl('/profile');
            })
        })
    }
}
