import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { AuthServiceAPI } from '../shared/auth.service';
import { EncryptService } from '../shared/encode.service';
import { NavbarComponent } from '../shared/navbar/navbar.component';
import { AuthService, GoogleLoginProvider } from 'angularx-social-login';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  focus;
  focus1;
  rememberMe: boolean;
  recaptchaVerified = false;
  
  @Input()
  password: ''
  userName: ''

  constructor(private router: Router,
              private authApi: AuthServiceAPI,
              private authService: AuthService,) { }

  ngOnInit() {
  }

  resolved(captchaResponse: string) {
    console.log(`Resolved captcha with response: ${captchaResponse}`);
    this.recaptchaVerified = true;
}

  login() {
    if (!this.recaptchaVerified) {
      alert("Please verify reCaptcha!");
      window.location.reload();
    } else {
    this.authApi.login(this.userName, this.password).subscribe((data) => {
      if (this.authApi.isLoggedIn()) {
        if(this.rememberMe) {
          this.authApi.rememberUser(this.userName);
        }
        console.log(this.authApi.getUpdated());
        if(this.authApi.getUpdated()){
          this.router.navigateByUrl('/landing');
        } else {
          alert("Update your profile.");
          this.router.navigateByUrl('/profile');
        }
       } else {
         alert("Password is incorrect");
       }
     }
   );
  }
  }

  googleLogin() {
    this.authService.signIn(GoogleLoginProvider.PROVIDER_ID).then(userData => {
      this.authApi.loginSocial(userData.email).subscribe((data) => {
        if (this.authApi.isLoggedIn()) {
          if(this.authApi.getUpdated()){
            this.router.navigateByUrl('/landing');
          } else {
            alert("Update your profile.");
            this.router.navigateByUrl('/profile');
          }
        }
      })
    });
  }

}
