import { Component, Inject } from '@angular/core';  
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { RestApiService } from '../shared/rest-api.service';
  
@Component({  
    selector: 'app-popup',  
    template: `  
    <style>
    .column {
        padding: 20px;
        margin-left: 20px;
    }
    .postValue {
        width: 680px;
    }
    </style>
            <div class="row justify-content-center">
                <ngb-carousel>
                    <ng-template ngbSlide *ngFor="let img of post.images">
                        <img [src]="getImage(img.pic)" alt="Image" height="150px" width="150px">
                    </ng-template>
                </ngb-carousel>
            </div>
            <div class="row">
                <div class="column justify-content-center">
                    <div class="row"><h2>{{post.title}}</h2></div>
                    <div class="row"><h5>{{post.description}}</h5></div>
                    <div class="row"><h6>Posted by: {{post.user.firstName}} {{post.user.lastName}}</h6></div>
                </div>
                <div class="column">
                    <button type="button" class="btn btn-link" (click)="toggleSelected(post)">
                        <span>
                            <i class="fa fa-heart-o" style="color: red; font-size: 24px;" *ngIf="!post.selected"></i>
                            <i class="fa fa-heart" style="color: red; font-size: 24px;" *ngIf="post.selected"></i>
                        </span>
                    </button>
                </div>
            </div>
            <div class="row">
                <input #bid name="bid" id="bid" type="number" style="width: 100px; padding: 8px; margin-bottom: 10px; margin-left: 10px;" [(ngModel)]="bidAmt" value="post.minBid">
                <button type="button" class="btn btn-success" style="margin-left: 20px;" (click)="bidPost(post)">
                    Bid
                </button>
            </div>
    `  
})  
export class PopupComponent{  
    bidAmt;
    constructor(@Inject(MAT_DIALOG_DATA) public post:any,
    private apiService : RestApiService){  
        console.log(post);
        post.selected = false;
    }

    getImage(img){
        return 'data:image/png;base64,'+ img;
    }

    toggleSelected(postData){
        console.log(postData);
        if(postData.selected==false){
          this.apiService.addFavorite(postData).subscribe((data)=>{
            if(data==true){ 
              postData.selected = true;
            }
            console.log(data);
          })
        } else {
          this.apiService.removeFavorite(postData).subscribe((data)=>{
            if(data==true){ 
              postData.selected = false;
            }
            console.log(data);
          })
        }
        
      }

      bidPost(post){
        if(post.minBid>this.bidAmt){
          alert("The min bid is "+post.minBid);
        } else {
          this.apiService.bidPost(post.id, this.bidAmt).subscribe((data)=>{
            alert("Success");
            window.location.reload();
            console.log(data);
          })
        }
      }
}