import { Component, OnInit, Input } from '@angular/core';
import { AuthServiceAPI } from '../shared/auth.service';
import { ActivatedRoute, Router } from '@angular/router';
import { EncryptService } from '../shared/encode.service';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css']
})
export class ResetPasswordComponent implements OnInit {

  user;
  passwordError;
  token;
  @Input()
  Re_password;
  password;

  constructor(private authApi: AuthServiceAPI,
    private route: ActivatedRoute,
    private router: Router,
    public encryption: EncryptService,) { }

  ngOnInit(): void {
    this.route.params.subscribe( (param) => {
      this.token = param.token;
      this.authApi.resetPasswordTokenVerify(param.token).subscribe((data) => {
          this.user = data;
          console.log(this.user);
        });
  });
  }

  onSubmit(){
    if(this.password == this.Re_password){
      var pass = this.encryption.encrypt(this.password);
      console.log(this.encryption.decrypt(this.user.password));
      if(this.encryption.decrypt(this.user.password) == this.password){
        this.passwordError = "New password cannot be same as old password.";
      } else {
        this.user.password = pass;
        this.authApi.resetPassword(this.user, this.token).subscribe(res=>{
          alert("Password changed successfully")
          this.router.navigateByUrl('/login');
        })
      }
    } else {
      this.passwordError = "Password do not match";
    }
  }

}
