
import { Injectable, NgModule } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { throwError } from 'rxjs';
import { map, catchError, retry } from 'rxjs/operators';
import { RememberMeService } from './remember.service';
import { EncryptService } from './encode.service';
import { CookieService } from 'ngx-cookie-service';
import { ConstantsService } from './constants.service';
import { Router } from '@angular/router';
import { LoginComponent } from '../login/login.component';

@Injectable({
  providedIn: 'root'
})

@NgModule({
  providers: [CookieService]
})
export class AuthServiceAPI {

  errorData: {};
  COOKIE_NAME = "rememberMe";
  SESSION_NAME = "currentUser";
  SECRET_KEY = "6LflJ_4UAAAAAATeer1Fx6Au2MRDTqQEscJPtINI";
  UPDATED = false;
  ISSOCIAL = false;

  constructor(private http: HttpClient,
              private enc: EncryptService,
              private remeberService: RememberMeService,
              private cookie: CookieService,
              private constant: ConstantsService,
              private router: Router) { }

  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      'Access-Control-Allow-Origin': "*"
    })
  }

  login(uname, pass) {
    return this.http.post<any>(this.constant.baseApiUrl+'/login', uname, this.httpOptions)
    .pipe(map(user => {
      var password = this.enc.decrypt(user.password);
      if (user && pass==password) {
        sessionStorage.setItem(this.SESSION_NAME, JSON.stringify(user.userName));
        if(user.country != null && user.phone != null){
          this.UPDATED = true;
        } else {
          this.router.navigateByUrl('/profile');
        }
      }
    }),
    catchError((error: HttpErrorResponse) => {
      if (error.status === 400) {
        alert("User not found.");
        //this.loginVar.loginError = "Username is incorrect";
        return throwError('User not found');
      } else {
        return throwError('backend comm error');
      }
    })
    );
  }

  loginSocial(uname) {
    return this.http.post<any>(this.constant.baseApiUrl+'/loginSocial', uname, this.httpOptions)
    .pipe(map(user => {
      if (user) {
        sessionStorage.setItem(this.SESSION_NAME, JSON.stringify(user.userName));
        this.ISSOCIAL = true;
        if(user.country != null && user.phone != null){
          this.UPDATED = true;
        } else {
          this.router.navigateByUrl('/profile');
        }
      }
    }),
    catchError((error: HttpErrorResponse) => {
      if (error.status === 400) {
        alert("User not found.");
        return throwError('User not found');
      } else {
        return throwError('backend comm error');
      }
    })
    );
  }

  isLoggedIn() {
    if (sessionStorage.getItem(this.SESSION_NAME) || this.loginRememberedUser()) {
      return true;
    }
    return false;
  }

  logout() {
    if(this.cookie.check(this.COOKIE_NAME)) {
      var id = this.cookie.get(this.COOKIE_NAME);
      this.remeberService.deleteRememberedUser(id);
      this.deleteRememberMeCookie();
    }
    sessionStorage.removeItem(this.SESSION_NAME);
  }

  rememberUser(username:string){
    var id = this.remeberService.rememberUser(username);
    this.cookie.set(this.COOKIE_NAME, id, 60 * 60 * 24 * 30, "/");
  }

  loginRememberedUser(): boolean{
    if(this.cookie.check(this.COOKIE_NAME)) {
      var id = this.cookie.get(this.COOKIE_NAME);
      var username = this.remeberService.getRememberedUser(id);
      if(username!=null) {
        sessionStorage.setItem(this.SESSION_NAME, JSON.stringify(username));
        return true;
      }
    }
    return false;
  }

  deleteRememberMeCookie() {
    this.cookie.delete(this.COOKIE_NAME, "/");
  }

  createUser(user) {
    console.log(this.constant.baseApiUrl);
    return this.http.post<any>(this.constant.baseApiUrl + '/register', JSON.stringify(user), this.httpOptions)
    .pipe(
      retry(1),
      catchError((error: HttpErrorResponse) => {
        if (error.status === 400) {
          alert("User already exists!");
          window.location.reload();
          return throwError('User Exists');
        } else {
          return throwError('backend comm error');
        }
      })
    )
  }  

  confirmUser(token) {
    return this.http.get<any>(this.constant.baseApiUrl+'/register/regitrationConfirm?token='+token, this.httpOptions)
    .pipe(catchError((error: HttpErrorResponse) => {
      if (error.status === 401) {
        alert("Invalid Verification token!");
        this.router.navigateByUrl("/");
        return throwError('Invalid Verification token!');
      } else if(error.status === 400) {
        alert("Verification link has expired");
        return throwError('Verification link has expired');
      } else {
        return throwError('backend comm error');
      }
    })
    );
  }

  updateUser(upload) {
    return this.http.post<any>(this.constant.baseApiUrl + '/update', upload)
    .pipe(
      retry(1),
      catchError((error: HttpErrorResponse) => {
          return throwError('backend comm error');
      })
    )
  }

  getUser() {
    return JSON.parse(sessionStorage.getItem(this.SESSION_NAME));
  }

  getUserDetails(uname) {
    return this.http.post<any>(this.constant.baseApiUrl+'/login', uname, this.httpOptions)
    .pipe(
    catchError((error: HttpErrorResponse) => {
      if (error.status === 400) {
        alert("User not found.");
        return throwError('User not found');
      } else {
        return throwError('backend comm error');
      }
    })
    );
  }

  getQuestion(uname) {
    return this.http.get<any>(this.constant.baseApiUrl+'/getQuestion?uname='+uname, this.httpOptions)
    .pipe(catchError((error: HttpErrorResponse) => {
      if(error.status === 400) {
        alert("User not found");
        window.location.reload();
        return throwError('User Not found');
      } else {
        return throwError('backend comm error');
      }
    })
    );
  }

  checkAnswer(data) {
    return this.http.post<any>(this.constant.baseApiUrl+'/checkAnswer', data)
    .pipe(catchError((error: HttpErrorResponse) => {
      if(error.status === 400) {
        alert("User not found");
        window.location.reload();
        return throwError('User Not found');
      } else {
        return throwError('backend comm error');
      }
    })
    );
  }

  resetPasswordTokenVerify(token) {
    return this.http.get<any>(this.constant.baseApiUrl+'/forgotPassword/reset?token='+token, this.httpOptions)
    .pipe(catchError((error: HttpErrorResponse) => {
      if (error.status === 401) {
        alert("Invalid Verification token!");
        this.router.navigateByUrl("/");
        return throwError('Invalid Verification token!');
      } else if(error.status === 400) {
        alert("Verification link has expired");
        this.router.navigateByUrl("/");
        return throwError('Verification link has expired');
      } else {
        return throwError('backend comm error');
      }
    })
    );
  }

  resetPassword(user, token) {
    return this.http.post<any>(this.constant.baseApiUrl + '/resetPassword?token='+token, JSON.stringify(user), this.httpOptions)
    .pipe(
      retry(1),
      catchError((error: HttpErrorResponse) => {
        if (error.status === 400) {
          alert("User already exists!");
          window.location.reload();
          return throwError('User Exists');
        } else {
          return throwError('backend comm error');
        }
      })
    )
  }

  getUpdated(){
    return this.UPDATED;
  }

  setUpdated(b:boolean){
    this.UPDATED = b;
  }

  isSocial(){
    return this.ISSOCIAL;
  }


}
