import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { ConstantsService } from './constants.service';
import { retry, catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})

export class RestApiService {
  
  COOKIE_NAME = "rememberMe";
  SESSION_NAME = "currentUser";
  constructor(private http: HttpClient,
              private constant: ConstantsService) { }

  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  }

  newPost(data) {
    return this.http.post<any>(this.constant.baseApiUrl + '/post/newPost?user='+this.getUser(), data)
    .pipe(
      retry(1),
      catchError((error: HttpErrorResponse) => {
          return throwError('backend comm error');
      })
    )
  }

  getPostForUser(){
    return this.http.get<any>(this.constant.baseApiUrl+"/post/userPosts?user="+this.getUser())
    .pipe(
      catchError((error: HttpErrorResponse) => {
        return throwError('backend comm error');
      })
    )
  }

  getPosts(){
    return this.http.get<any>(this.constant.baseApiUrl+"/post/posts?user="+this.getUser())
    .pipe(
      catchError((error: HttpErrorResponse) => {
        return throwError('backend comm error');
      })
    )
  }

  getUser() {
    return JSON.parse(sessionStorage.getItem(this.SESSION_NAME));
  }

  addFavorite(postData){
    return this.http.post<any>(this.constant.baseApiUrl + '/favoritePost?user='+this.getUser(), postData)
    .pipe(
      retry(1),
      catchError((error: HttpErrorResponse) => {
          return throwError('backend comm error');
      })
    )
  }

  removeFavorite(postData){
    return this.http.post<any>(this.constant.baseApiUrl + '/deleteFavoritePost?user='+this.getUser(), postData)
    .pipe(
      retry(1),
      catchError((error: HttpErrorResponse) => {
          return throwError('backend comm error');
      })
    )
  }

  getFavorite(){
    return this.http.get<any>(this.constant.baseApiUrl + '/favorites?user='+this.getUser())
    .pipe(
      retry(1),
      catchError((error: HttpErrorResponse) => {
          return throwError('backend comm error');
      })
    )
  }

  getTitles(){
    return this.http.get<any>(this.constant.baseApiUrl + '/post/titles?user='+this.getUser())
    .pipe(
      retry(1),
      catchError((error: HttpErrorResponse) => {
          return throwError('backend comm error');
      })
    )
  }

  getSearchResults(search){
    return this.http.get<any>(this.constant.baseApiUrl + '/post/search?user='+this.getUser()+'&search='+search)
    .pipe(
      retry(1),
      catchError((error: HttpErrorResponse) => {
          return throwError('backend comm error');
      })
    )
  }

  bidPost(postId, bidAmt){
    return this.http.get<any>(this.constant.baseApiUrl + '/post/bid?user='+this.getUser()+'&postId='+postId+'&bidAmt='+bidAmt)
    .pipe(
      retry(1),
      catchError((error: HttpErrorResponse) => {
          return throwError('backend comm error');
      })
    )
  }
}