import { Component, OnInit } from '@angular/core';
import { AuthServiceAPI } from '../shared/auth.service';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {

  security = false;
  isNull = false;
  securityQuestion : string;
  wrongAnswer = false;
  answer: string;
  userName : string;
  submitted = false;

  constructor(private authApi : AuthServiceAPI) { }

  ngOnInit(): void {
  }

  onSubmit() {
    this.submitted = true;
    if(this.userName == null){
      this.isNull = true;
    } else {
      this.authApi.getQuestion(this.userName).subscribe((data)=>{
        console.log(data);
        this.submitted = false;
        this.securityQuestion = data.question;
        this.security = true;
      })
    }
  }

  checkAnswer(){
    this.wrongAnswer = false;
    if(this.userName == null){
      this.isNull = true;
    } else {
      this.submitted = true;
      var data = new FormData();
      data.append("userName",this.userName);
      data.append("answer",this.answer);
      this.authApi.checkAnswer(data).subscribe((res)=>{
        if(res==false){
          this.wrongAnswer = true;
          this.submitted = false;
        } else {
          alert("Email has been sent");
          window.location.reload();
        }
      })
    }
  }

}
