import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd, NavigationStart } from '@angular/router';
import { Location, PopStateEvent } from '@angular/common';
import { AuthServiceAPI } from '../auth.service';
import { AuthService, GoogleLoginProvider } from 'angularx-social-login';
import {MatAutocompleteModule} from '@angular/material/autocomplete';
import { RestApiService } from '../rest-api.service';
import { Observable } from 'rxjs';

@Component({
    selector: 'app-navbar',
    templateUrl: './navbar.component.html',
    styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {
    public isCollapsed = true;
    private lastPoppedUrl: string;
    private yScrollStack: number[] = [];
    titles = [];
    filteredTitle;
    filterValue;

    constructor(public location: Location,
        private router: Router,
        public authApi: AuthServiceAPI,
        private authService: AuthService,
        private restApi: RestApiService) {
    }

    ngOnInit() {
      this.router.events.subscribe((event) => {
        this.isCollapsed = true;
        if (event instanceof NavigationStart) {
           if (event.url != this.lastPoppedUrl)
               this.yScrollStack.push(window.scrollY);
       } else if (event instanceof NavigationEnd) {
           if (event.url == this.lastPoppedUrl) {
               this.lastPoppedUrl = undefined;
               window.scrollTo(0, this.yScrollStack.pop());
           } else
               window.scrollTo(0, 0);
       }
     });
     this.location.subscribe((ev:PopStateEvent) => {
         this.lastPoppedUrl = ev.url;
     });

     if(this.authApi.isLoggedIn()) {
        this.restApi.getTitles().subscribe((data)=>{
            this.titles = data;
        });
    }

    }

    logout() {
        if(this.authApi.isLoggedIn()) {
            this.authApi.logout();
            if(this.authApi.isSocial()){
                this.authService.signOut();
            }
            this.router.navigateByUrl('');
        }
    }

    filterTitleValue(searchValue: string){
        this.filterValue = searchValue.toLowerCase();
        this.filteredTitle = this.titles.filter(option => option.toLowerCase().includes(this.filterValue));
    }

    searchButton(searchValue: string){
        this.router.navigateByUrl('/search/'+searchValue);
    }

}
