import { Component, OnInit } from '@angular/core';
import { RestApiService } from '../shared/rest-api.service';
import { NgbCarouselConfig } from '@ng-bootstrap/ng-bootstrap';
import { AuthServiceAPI } from '../shared/auth.service';
import { Router } from '@angular/router';

@Component({
    selector: 'app-landing',
    templateUrl: './landing.component.html',
    styleUrls: ['./landing.component.css']
})

export class LandingComponent implements OnInit {
  focus: any;
  focus1: any;
  userPosts;
  noPosts;
  bidAmt;
  
  constructor(private apiService : RestApiService,
    private config: NgbCarouselConfig,
    private authApi: AuthServiceAPI,
    private router: Router) { 
      config.interval = 3000;
      config.pauseOnHover = true;
    }

  ngOnInit() {
    if(!this.authApi.isLoggedIn()){
      this.router.navigateByUrl('/');
    }
    this.apiService.getPosts().subscribe((data) => {
      if(data.length>0) {
        this.noPosts = false;
        this.userPosts = data;
        this.userPosts.forEach(post => {
          post.selected = false;
        });
        console.log(this.userPosts);
      }
      else {
        console.log(this.noPosts)
        this.noPosts=true;
      }
    })
  }

  getImage(img){
    return 'data:image/png;base64,'+ img;
  }

  toggleSelected(postData){
    console.log(postData);
    if(postData.selected==false){
      this.apiService.addFavorite(postData).subscribe((data)=>{
        if(data==true){ 
          postData.selected = true;
        }
        console.log(data);
      })
    } else {
      this.apiService.removeFavorite(postData).subscribe((data)=>{
        if(data==true){ 
          postData.selected = false;
        }
        console.log(data);
      })
    }
    
  }

  bidPost(post){
    if(post.minBid>post.bidAmt){
      alert("The min bid is "+post.minBid);
    } else {
      this.apiService.bidPost(post.id, post.bidAmt).subscribe((data)=>{
        alert("Success");
        window.location.reload();
        console.log(data);
      })
    }
  }

}
