import { Component, OnInit } from '@angular/core';
import { CompressorService } from '../shared/compressor.service';
import { map, expand } from 'rxjs/operators';
import { EMPTY } from 'rxjs';
import { RestApiService } from '../shared/rest-api.service';
import { NgbCarouselConfig } from '@ng-bootstrap/ng-bootstrap';
import { AuthServiceAPI } from '../shared/auth.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-posts',
  templateUrl: './posts.component.html',
  styleUrls: ['./posts.component.css']
})
export class PostsComponent implements OnInit {

  noPosts;
  newPost = false;
  posted = false;
  maxFiles = '';
  files = [];
  imgURL = [];
  data = {title: '', description: '', minBid: 0}
  uploadData = new FormData();
  reader = new FileReader();
  userPosts;

  constructor(private resizer: CompressorService,
              private apiService : RestApiService,
              private config: NgbCarouselConfig,
              private authApi: AuthServiceAPI,
              private router: Router) { 
                config.interval = 3000;
                config.pauseOnHover = true;
              }

  ngOnInit(): void {
    if(!this.authApi.isLoggedIn()){
      this.router.navigateByUrl('/');
    }
    this.apiService.getPostForUser().subscribe((data) => {
      if(data.length>0) {
        this.noPosts = false;
        console.log(data);
        this.userPosts = data;
      } else {
        this.noPosts = true;
      }
    })
  }

  recursiveCompress = (image: File, index, array) => {
    return this.resizer.compress(image).pipe (
      map(response => {
        console.log('compressed ' + index + image.name);
        this.reader.readAsDataURL(response);
        this.uploadData.append("files", response);
        this.files.push(response);
        this.reader.onload = ev => {
          this.imgURL.push(this.reader.result);
        }
        return {
          data: response,
          index: index + 1,
          array: array,
        };
      }),
    );
  }

  onFileDropped($event) {
    console.log($event)
    this.prepareFilesList($event);
  }

  prepareFilesList(files: Array<any>) {
    if(this.files.length < 3 && files.length + this.files.length <=3 ){
      const compress = this.recursiveCompress( files[0], 0, files ).pipe(
        expand(res => {
          return res.index > res.array.length - 1
            ? EMPTY
            : this.recursiveCompress( files[res.index], res.index, files );
        }),
      );
      compress.subscribe(res => {
        if (res.index > res.array.length - 1) {
          console.log(this.imgURL);
          }
        });
      } else {
      this.maxFiles = 'Maximum 3 files allowed.';
    }
    
  }

  newPostFunction(){
    this.newPost = !this.newPost;
  }

  post(){
    this.posted = true;
    this.uploadData.append("title", this.data.title);
    this.uploadData.append("description", this.data.description);
    this.uploadData.append("minBid", this.data.minBid.toString());
    this.uploadData.append("body", JSON.stringify(this.data))
    this.apiService.newPost(this.uploadData).subscribe((data)=>{
      console.log(data);
      window.location.reload();
    })
  }

  getImage(img){
    return 'data:image/png;base64,'+ img;
  }
}
