import { Injectable } from '@angular/core';
import * as CryptoJS from 'crypto-js';
import {Md5} from 'ts-md5/dist/md5';
import { ConstantsService } from './constants.service';

@Injectable({
    providedIn: 'root'
  })

export class EncryptService {

    constructor(private constant : ConstantsService) { }
    encrypt(text) {
        var encText = CryptoJS.AES.encrypt(text, this.constant.key);
        return encText.toString();
      }

      decrypt(text) {
        var encText = CryptoJS.AES.decrypt(text, this.constant.key);
        return encText.toString(CryptoJS.enc.Utf8);
      }
}