import { Component, OnInit, Input } from '@angular/core';
import {ActivatedRoute, Router} from "@angular/router";
import { AuthServiceAPI } from '../shared/auth.service';

@Component({
    selector: 'app-register-confirm',
    templateUrl: './registrationConfirm.component.html',
    styleUrls: ['./registrationConfirm.component.scss']
})
export class RegisterConfirmComponent implements OnInit {

    constructor(private authApi: AuthServiceAPI,
                private route: ActivatedRoute,
                private router: Router) { 
                }

    ngOnInit() {
    }

    register(){
        this.route.params.subscribe( (param) => {
            this.authApi.confirmUser(param.token).subscribe((data) => {
                console.log(JSON.stringify(data));
                alert("Account verified");
                this.router.navigateByUrl('/profile');
              });
        });
    }

}